"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { Menu } from "lucide-react";

import { authClient } from "@acme/auth/client";
import { Avatar, AvatarFallback, AvatarImage } from "@acme/ui/avatar";
import { Button } from "@acme/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@acme/ui/dropdown-menu";
import {
  NavigationMenu,
  NavigationMenuContent,
  NavigationMenuItem,
  NavigationMenuLink,
  NavigationMenuList,
  NavigationMenuTrigger,
} from "@acme/ui/navigation-menu";
import {
  Sheet,
  SheetClose,
  SheetContent,
  SheetDescription,
  SheetFooter,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@acme/ui/sheet";

// Define a type for the user that matches what we get from the session
type UserData = {
  id?: string;
  name?: string | null;
  email?: string | null;
  image?: string | null;
};

export default function Navbar() {
  const [user, setUser] = useState<UserData | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchSession = async () => {
      try {
        const sessionData = await authClient.getSession();
        if (sessionData?.data?.user) {
          // Extract only the properties we need
          const { name, email, image } = sessionData.data.user;
          setUser({ name, email, image });
        } else {
          setUser(null);
        }
      } catch (error) {
        console.error("Error fetching session:", error);
        setUser(null);
      } finally {
        setIsLoading(false);
      }
    };

    void fetchSession();
  }, []);

  const UserAvatar = () => (
    <div className="flex items-center">
      {isLoading ? (
        <div className="h-8 w-8 animate-pulse rounded-full bg-muted"></div>
      ) : user ? (
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="ghost" className="relative h-8 w-8 rounded-full">
              <Avatar className="h-8 w-8">
                {user.image ? (
                  <AvatarImage src={user.image} alt={user.name || "User"} />
                ) : (
                  <AvatarFallback>
                    {user.name
                      ? user.name.charAt(0)
                      : user.email
                        ? user.email.charAt(0)
                        : "U"}
                  </AvatarFallback>
                )}
              </Avatar>
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end">
            <DropdownMenuLabel>
              {user.name || user.email || "My Account"}
            </DropdownMenuLabel>
            <DropdownMenuSeparator />
            <DropdownMenuItem asChild>
              <Link href="/dashboard">Dashboard</Link>
            </DropdownMenuItem>
            <DropdownMenuItem asChild>
              <Link href="/settings">Settings</Link>
            </DropdownMenuItem>
            <DropdownMenuSeparator />
            <DropdownMenuItem
              onClick={() =>
                void authClient.signOut({ query: { callbackUrl: "/" } })
              }
            >
              Log out
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      ) : (
        <div className="flex space-x-2">
          <Button variant="outline" className="hover:bg-transparent" asChild>
            <Link href="/login">Log in</Link>
          </Button>
          <Button asChild>
            <Link href="/signup">Sign up</Link>
          </Button>
        </div>
      )}
    </div>
  );

  return (
    <div className="fixed left-0 right-0 top-0 z-50 w-full border-b border-border/40 bg-background/95 px-4 py-4 shadow-sm backdrop-blur-sm">
      <div className="mx-auto flex max-w-6xl items-center justify-between">
        <Link href="/" className="text-xl font-bold hover:opacity-80">
          comment.video
        </Link>

        {/* Desktop Navigation */}
        <NavigationMenu className="hidden lg:flex">
          <NavigationMenuList className="space-x-0.3">
            <NavigationMenuItem>
              <NavigationMenuTrigger>Product</NavigationMenuTrigger>
              <NavigationMenuContent>
                <div className="w-[200px] p-4">
                  <ul className="space-y-2">
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/product/overview"
                          className="block hover:text-primary"
                        >
                          Overview
                        </Link>
                      </NavigationMenuLink>
                    </li>
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/product/features"
                          className="block hover:text-primary"
                        >
                          Features
                        </Link>
                      </NavigationMenuLink>
                    </li>
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/product/pricing"
                          className="block hover:text-primary"
                        >
                          Pricing
                        </Link>
                      </NavigationMenuLink>
                    </li>
                  </ul>
                </div>
              </NavigationMenuContent>
            </NavigationMenuItem>

            <NavigationMenuItem>
              <NavigationMenuTrigger>Resources</NavigationMenuTrigger>
              <NavigationMenuContent>
                <div className="w-[200px] p-4">
                  <ul className="space-y-2">
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/resources/blog"
                          className="block hover:text-primary"
                        >
                          Blog
                        </Link>
                      </NavigationMenuLink>
                    </li>
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/resources/docs"
                          className="block hover:text-primary"
                        >
                          Docs
                        </Link>
                      </NavigationMenuLink>
                    </li>
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/resources/support"
                          className="block hover:text-primary"
                        >
                          Support
                        </Link>
                      </NavigationMenuLink>
                    </li>
                  </ul>
                </div>
              </NavigationMenuContent>
            </NavigationMenuItem>

            <NavigationMenuItem>
              <NavigationMenuTrigger>Company</NavigationMenuTrigger>
              <NavigationMenuContent>
                <div className="w-[200px] p-4">
                  <ul className="space-y-2">
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/company/about"
                          className="block hover:text-primary"
                        >
                          About
                        </Link>
                      </NavigationMenuLink>
                    </li>
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/company/careers"
                          className="block hover:text-primary"
                        >
                          Careers
                        </Link>
                      </NavigationMenuLink>
                    </li>
                  </ul>
                </div>
              </NavigationMenuContent>
            </NavigationMenuItem>

            <NavigationMenuItem>
              <NavigationMenuTrigger>Enterprise</NavigationMenuTrigger>
              <NavigationMenuContent>
                <div className="w-[200px] p-4">
                  <ul className="space-y-2">
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/enterprise/solutions"
                          className="block hover:text-primary"
                        >
                          Solutions
                        </Link>
                      </NavigationMenuLink>
                    </li>
                    <li>
                      <NavigationMenuLink asChild>
                        <Link
                          href="/enterprise/security"
                          className="block hover:text-primary"
                        >
                          Security
                        </Link>
                      </NavigationMenuLink>
                    </li>
                  </ul>
                </div>
              </NavigationMenuContent>
            </NavigationMenuItem>

            <NavigationMenuItem>
              <NavigationMenuLink
                asChild
                className="inline-flex items-center justify-center rounded-md px-3 py-2 text-sm font-medium transition-colors hover:bg-accent hover:text-accent-foreground focus:outline-none data-[active]:bg-accent data-[state=open]:bg-accent data-[active]:text-accent-foreground data-[state=open]:text-accent-foreground"
              >
                <Link href="/pricing" className="hover:text-primary">
                  Pricing
                </Link>
              </NavigationMenuLink>
            </NavigationMenuItem>
          </NavigationMenuList>
        </NavigationMenu>

        {/* Desktop User Menu */}
        <div className="hidden md:flex">
          <UserAvatar />
        </div>

        {/* Mobile Menu */}
        <div className="flex items-center md:hidden">
          <Sheet>
            <SheetTrigger asChild>
              <Button variant="ghost" size="icon">
                <Menu className="h-6 w-6" />
                <span className="sr-only">Toggle menu</span>
              </Button>
            </SheetTrigger>
            <SheetContent side="right" className="w-[80%] sm:w-[350px]">
              <SheetHeader>
                <SheetTitle>Menu</SheetTitle>
                <SheetDescription>
                  Navigate through our site or manage your account.
                </SheetDescription>
              </SheetHeader>
              <div className="mt-6 flex flex-col gap-4">
                <div className="mb-6 flex justify-center">
                  <UserAvatar />
                </div>
                <nav className="flex flex-col space-y-2">
                  <SheetClose asChild>
                    <Link
                      href="/product/overview"
                      className="rounded-md px-4 py-2 hover:bg-accent"
                    >
                      Product Overview
                    </Link>
                  </SheetClose>
                  <SheetClose asChild>
                    <Link
                      href="/product/features"
                      className="rounded-md px-4 py-2 hover:bg-accent"
                    >
                      Features
                    </Link>
                  </SheetClose>
                  <SheetClose asChild>
                    <Link
                      href="/product/pricing"
                      className="rounded-md px-4 py-2 hover:bg-accent"
                    >
                      Pricing
                    </Link>
                  </SheetClose>
                  <SheetClose asChild>
                    <Link
                      href="/resources/blog"
                      className="rounded-md px-4 py-2 hover:bg-accent"
                    >
                      Blog
                    </Link>
                  </SheetClose>
                  <SheetClose asChild>
                    <Link
                      href="/resources/docs"
                      className="rounded-md px-4 py-2 hover:bg-accent"
                    >
                      Docs
                    </Link>
                  </SheetClose>
                  <SheetClose asChild>
                    <Link
                      href="/company/about"
                      className="rounded-md px-4 py-2 hover:bg-accent"
                    >
                      About
                    </Link>
                  </SheetClose>
                </nav>
              </div>
              <SheetFooter className="mt-auto pt-4">
                <p className="text-center text-xs text-muted-foreground">
                  Â© {new Date().getFullYear()} comment.video
                </p>
              </SheetFooter>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </div>
  );
}
